.job-question .custom-control-input:checked~.custom-control-label::before {
    color: #fff;
    border-color: #E85A19 !important;
    background-color: #E85A19 !important;
}
.job-question .custom-switch .custom-control-label::before {
    border: 1px solid #adb5bd !important;
}
.job-question .custom-control-input:focus~.custom-control-label::before {
    box-shadow: 0 0 0 0.2rem #e85b1925 !important;
}
.second-title-mob{
    display: none ;
}
@media only screen and (max-width: 414px) {
    .second-title-mob{
        display: block;
    }
    .second-title-web{
        display: none;
    }
}