.w-fit-content{
    width: fit-content !important;
}
.text-editor-container{
    transition: 0.5s;
}
.text-editor-btn:hover {
    color: #F27016 !important;
}
.text-input-container .form-control:focus{
    border:none !important;
    box-shadow: 0 0 0 0.1rem rgb(247, 247, 247);
}
.text-input-container #text-input{
    border:none !important;
    color: white;
    background-color: #040404ba;
    width: 100%;
    border-radius: 0.25rem;
    font-size: 1rem;
    outline: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}
.text-input-container #text-input:focus{
    border:none !important;
    box-shadow: none !important;
}
#output{
    resize: both !important;
    overflow: hidden !important;
}
.bg-darkblue{
    background-color: #152B39 !important;
}
.addons-list li:hover ,
.addons-list li:focus {
    background-color: rgb(242,242,242) !important;
}

.pulse {
    margin:100px;
    display: block;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background: #ffffff;
    cursor: pointer;
    box-shadow: 0 0 0 #040404ba;
    animation: pulse 2s infinite;
  }
  .pulse:hover {
    animation: none;
  }
.bg-dark-transperant{
    background-color: #040404ba!important;
}
.addon-images .bg-dark-transperant:hover ,
.addon-text .bg-dark-transperant:hover, 
.addon-images .bg-dark-transperant:focus,
.addon-text .bg-dark-transperant:hover 
    {
     background-color: #0404047e !important;
  }
.text-editor-container .bg-dark-transperant:hover {
   background-color: #040404ba !important; 
}
.text-editor-container .MuiInput-underline:before,
.text-editor-container .MuiInput-underline::after{
  left: unset;
  right: unset;
  bottom: unset;
  content: unset;
}
.text-editor-container .MuiSelect-icon{
  color: white !important;
}
.text-editor-container .MuiSelect-select{
  color: #F27016 !important;
}
.text-editor-container .MuiSelect-select.MuiSelect-select
{
  padding-left: 1rem;
}

.addon-text .close{
  color: #fff;
}
  video::-webkit-slider-thumb {
    background-color: red !important;
    opacity: 2 !important;
    display: block !important;
  }
  input[type="range" i]::-webkit-slider-thumb {
    -webkit-appearance: sliderthumb-horizontal;
    box-sizing: border-box;
    display: block;
    -webkit-user-modify: initial !important;
    }
    .addon-textarea ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: white;
      opacity: 1; /* Firefox */
    }
    @media only screen and (max-width: 600px){
      .sidebar-bottom-responsive{
        position: relative !important;
        bottom: unset !important;
        text-align: right !important;
        padding: .5rem!important;
      }
    }
  @-webkit-keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 #ffffff57;
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px #ffffff00;
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 #ffffff00;
    }
  }
  @keyframes pulse {
    0% {
      -moz-box-shadow: 0 0 0 0 #ffffff57;
      box-shadow: 0 0 0 0 #ffffff57;
    }
    70% {
        -moz-box-shadow: 0 0 0 10px #ffffff00;
        box-shadow: 0 0 0 10px #ffffff00;
    }
    100% {
        -moz-box-shadow: 0 0 0 0 #ffffff00;
        box-shadow: 0 0 0 0 #ffffff00;
    }
  }